<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Assign Driver
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Assign Driver</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->

<div class="col-md-12">
<div class="card">
              <div class="card-header">
                <div class="row">
                  <div class="col-sm-6"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
              </div>
              </div>
              <!-- /.card-header -->
              <div class="card-body p-0">
                <table class="table table-condensed">
                  <tr>
                    <th style="width: 12%">BookingID</th>
                    <th style="width: 13%">Booking Date</th>
                    <th style="width: 12%">Booking Time</th>
                    <th style="width: 12%">User Name</th>
                    <th style="width: 25%">Address</th>
                    <th style="width: 15%">Driver</th>
                    <th style="width: 15%;">Action</th>
                  </tr>
            <tbody>
                <tr v-for="(item,index) in filteredCustomers">
                    <td>{{ item.BookingId }}</td>
                    <td>{{ item.BDate }}</td>
                    <td>{{ item.BTime }}</td>
                    <td>{{ item.Username }}</td>
                    <td>{{ item.FromAddress }}</td>
                    <td v-if="item.Drivermobile===''"><select v-model="drivermobno" class="form-control select2" style="width: 100%;" required>
                      <option  v-for="dept in listDriver" v-bind:value="dept.driver_phone">
                      {{dept.driver_phone}}
                      </option>
                    </select></td>
                    <td v-else><select class="form-control" style="width: 100%;" disabled>
                      <option  value="item.Drivermobile">{{item.Drivermobile}}</option>
                    </select></td>
                    <td v-if="item.Drivermobile===''">
                    <div class="btn-group"><button class="btn btn-success btn-sm" v-on:click="deleteItem(item.BookingId,item.Mobileno,index)">Assign</button>
                    <button class="btn btn-danger btn-sm" v-on:click="">Info</button></div>                    
                    </td>
                    <td v-else>
                    <div class="btn-group">
                    <button class="btn btn-danger btn-sm" v-on:click="">Info</button></div>                    
                    </td>
                </tr>
            </tbody>

                </table>
              </div>
              <!-- /.card-body -->
            </div>
</div>
</div>
</div>

</template>

<script>
export default {
  name: 'AssignDrivervw',
  data () { return {items: [], search: '', listDriver: [], data: {}, drivermobno: ''} },
  computed:
  {
    filteredCustomers: function () {
      var self = this
      return this.items.filter(function (cust) { return cust.BookingId.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 })
       // return this.customers;
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: { FetchItem () {
    // console.log();
    let uri1 = this.$hostName + '/api/driver/viewDriver'
    this.axios.get(uri1).then((response) => {
      console.log(response.data)
      this.listDriver = response.data
    })

    let uri = this.$hostName + '/api/driver/NoAssignBooking'
    this.axios.get(uri).then((response) => {
      this.items = response.data
    })
  },
    deleteItem (id1, mobno, ri) {
      var r = confirm('Are u want to Assign Booking?')
      if (r === true) {
        let uri = this.$hostName + '/api/courier/DriverAssign'
        this.data.booktype = 'Courier'
        this.data.bid = id1
        this.data.userid = ''
        this.data.mobno = mobno
        this.data.driverid = ''
        this.data.drivermobile = this.drivermobno
        console.log(this.data)
        this.axios.post(uri, this.data).then((response) => {
                // console.log(response.data);
          location.reload()
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



