import AgentMastervw from '../components/agent/AgentMastervw.vue'
import AgentMasteradd from '../components/agent/AgentMasteradd.vue'
const AdminRoutes = [
  {
    path: '/admin/AgentMaster',
    name: 'AgentMastervw',
    component: AgentMastervw
  },
  {
    path: '/admin/AgentMaster/add',
    name: 'AgentMasteradd',
    component: AgentMasteradd
  }
]

export default AdminRoutes



