<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Booking History
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Booking History</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->

<div class="col-md-12">
<div class="card">
              <div class="card-header">
                <div class="row">
                  <div class="col-sm-4"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
                  <div class="col-sm-1"><h4 class="text-left">From : </h4></div>
                  <div class="col-sm-3"><h4 class="text-left"><datepicker :value="fromdate" placeholder="None" format="dd-MM-yyyy" @selected="onDateSelect1" style="width:50px"></datepicker></h4></div>
                  
                  <div class="col-sm-1"><h4 class="text-left">To : </h4></div>
                  <div class="col-sm-1"><h4 class="text-left">
                  <div style="width:100px"><datepicker :value="todate" placeholder="None" format="dd-MM-yyyy" @selected="onDateSelect2" style="width:150px"></datepicker></div>
                  </h4></div>
                  <div class="col-sm-12" style="float:right;margin-right:10px;"><h4 class="text-left"><button class="btn-success" type="button" v-on:click="FetchItem()">Submit</button></h4></div>
                  <date-picker v-model="card"></date-picker>
              </div>
              </div>
              <!-- /.card-header -->
              <div class="card-body p-0">
                <table class="table table-condensed">
                  <tr>
                    <th style="width: 12%">BookingID</th>
                    <th style="width: 12%">Booking Type</th>
                    <th style="width: 13%">Booking Date</th>
                    <th style="width: 12%">Booking Time</th>
                    <th style="width: 15%">User Name</th>
                    <th style="width: 15%">Driver Name</th>
                    <th style="width: 12%">Pay Mode</th>
                    <th style="width: 20%">Amount</th>
                  </tr>
            <tbody>
                <tr v-for="(item,index) in filteredCustomers">
                    <td>{{ item.BookingId }}</td>
                    <td>{{ item.bookingType }}</td>
                    <td>{{ item.BDate }}</td>
                    <td>{{ item.BTime }}</td>
                    <td>{{ item.Name }}</td>
                    <td>{{ item.driver_name }}</td>
                    <td>{{ item.paymentMode }}</td>
                    <td>{{ item.driverEarnings }}</td>                    
                    
                </tr>
            </tbody>

                </table>
              </div>
              <!-- /.card-body -->
            </div>
</div>
</div>
</div>

</template>

<script>
import Datepicker from 'vuejs-datepicker'
import datePicker1 from 'vue-bootstrap-datetimepicker'
import moment from 'moment'

export default {
  name: 'AssignDrivervw',
  components: { datePicker1, Datepicker },
  data () { return {items: [], search: '', listDriver: [], data: {}, drivermobno: '', fromdate: '', todate: '', myDate: ''} },
  computed:
  {
    filteredCustomers: function () {
      var self = this
      return this.items.filter(function (cust) { return cust.BookingId.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 })
       // return this.customers;
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: { FetchItem () {
    // console.log();
    console.log(this.fromdate)
    console.log(this.todate)
    let uri = this.$hostName + '/api/driver/DriverReport'
    this.data.fromdate = this.fromdate
    this.data.todate = this.todate
    this.axios.post(uri, this.data).then((response) => {
      console.log(response.data)
      this.items = response.data
    })
  },
    deleteItem (id1, mobno, ri) {
      var r = confirm('Are u want to Assign Booking?')
      if (r === true) {
        let uri = this.$hostName + '/api/courier/DriverAssign'
        this.data.booktype = 'Courier'
        this.data.bid = id1
        this.data.userid = ''
        this.data.mobno = mobno
        this.data.driverid = ''
        this.data.drivermobile = this.drivermobno
        console.log(this.data)
        this.axios.post(uri, this.data).then((response) => {
                // console.log(response.data);
          location.reload()
        })
      }
    },
    fixDate (event) {
      alert(moment(event).format('YYYY-MM-DD'))
    },
    onDateSelect1 (date) {
      this.fromdate = moment(date).format('YYYY/MM/DD')
    },
    onDateSelect2 (date) {
      this.todate = moment(date).format('YYYY/MM/DD')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



