<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Agent Master
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Agent Master</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Agent</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">Agent ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Agent Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Agent Name" v-model="cname" v-autofocus required>
                  </div>
                  <div class="form-group row" style="line-height: 2em">
                    <div class="col-md-4"><label for="exampleInputPassword1">Gender</label></div>
                    <div class="col-md-3"><input type="radio" name="act" class="form-check-input" value="Courier" v-model="ccourier">Male</div>
                    <div class="col-md-3"><input type="radio" name="act" class="form-check-input" value="Parcel" v-model="cparcel">Female </div>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Father's Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Father's Name" v-model="fname">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Date of Birth</label>
                    <input type="date" class="form-control" id="exampleInputPassword1" placeholder="Enter DOB" v-model="dob" value="YYYY-MM-DD">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Address</label>
                    <textarea class="form-control" id="exampleInputPassword1" placeholder="Enter Address" v-model="desc" ></textarea>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Mobile No</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter MobileNo" v-model="fname">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Email</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter EmailId" v-model="fname">
                  </div>                  
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'AgentMasteradd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', cname: '', desc: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/agent/Agent/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/agent/Agent/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.cname = response.data[0]['Agent']
          this.desc = response.data[0]['Description']
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/agent/Agent/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/agent/Agent/update' }
      this.item.id = this.id
      this.item.cname = this.cname
      this.item.desc = this.desc
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'AgentMastervw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



